.header {
  min-height: 70px;
  display: flex;
  align-items: center;
}

.hamburger-holder {
  //display: none;

  .hamburger {
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;

    .line {
      position: absolute;
      left: 50%;
      transform: translate(-50%);
      width: 100%;
      height: 2px;
      background: #000;
    }

    .line1 {
      top: 0;
    }

    .line2 {
      top: 50%;
    }
    .line3 {
      top: 100%;
    }
  }
}

.header-holder {
  display: flex;
  align-content: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;

  strong {
    padding: 0 0 0 10px;
  }
}

.menu-holder {
  display: flex;
  align-items: center;
}

.menu {
  display: flex;
  li {
    padding: 0 13px;


    a {
      text-transform: uppercase;
      font-size: 12px;
      transition: color 0.3s;
      &:hover {
        color: #FFA300;
      }
    }
  }
}

.contacts {
  display: flex;
  align-items: center;
  font-size: 16px;
}

.social_list {
  display: flex;
  align-items: center;
  li {
    padding: 5px;

    a {
      opacity: 1;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.5;
      }
    }
  }
}